@font-face {
  font-family: 'iconfont';
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot");
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot?#iefix") format("embedded-opentype"),url("//at.alicdn.com/t/font_1467357626_5109937.woff") format("woff"),url("//at.alicdn.com/t/font_1467357626_5109937.ttf") format("truetype"),url("//at.alicdn.com/t/font_1467357626_5109937.svg#iconfont") format("svg");
}

.main-theme-img {
  margin-top: 1.38889rem;
  height: 6.25rem;
  position: relative;
  -webkit-box-shadow: 0 0 2.667rem #646464 inset;
  box-shadow: 0 0 2.667rem #646464 inset;
  overflow: hidden;
}

.main-theme-img>img {
  -webkit-animation: move 20s linear infinite alternate;
  animation: move 20s linear infinite alternate;
}

@-webkit-keyframes move {
  0% {
    -webkit-transform: scale(1, 1) translate(-0.267rem, -0.267rem);
    transform: scale(1, 1) translate(-0.267rem, -0.267rem);
  }

  85% {
    -webkit-transform: scale(1.2, 1.2) translate(0.267rem, 0.267rem);
    transform: scale(1.2, 1.2) translate(0.267rem, 0.267rem);
  }
}

@keyframes move {
  0% {
    -webkit-transform: scale(1, 1) translate(-0.267rem, -0.267rem);
    transform: scale(1, 1) translate(-0.267rem, -0.267rem);
  }

  85% {
    -webkit-transform: scale(1.2, 1.2) translate(0.267rem, 0.267rem);
    transform: scale(1.2, 1.2) translate(0.267rem, 0.267rem);
  }
}

.main-theme-img h2 {
  position: absolute;
  bottom: .74074rem;
  left: .27778rem;
  font-size: 0.48rem;
  color: #fff;
}

.theme-list {
  padding: 0 .18519rem;
}

.theme-list li.theme-editor {
  margin: .18519rem;
  height: 1.01852rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.theme-list li.theme-editor span {
  font-size: 0.373rem;
  color: #969696;
}

.theme-list li.theme-editor img {
  margin-left: .37037rem;
  height: .78704rem;
  width: .78704rem;
  border-radius: 50%;
}


