@font-face {
  font-family: 'iconfont';
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot");
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot?#iefix") format("embedded-opentype"),url("//at.alicdn.com/t/font_1467357626_5109937.woff") format("woff"),url("//at.alicdn.com/t/font_1467357626_5109937.ttf") format("truetype"),url("//at.alicdn.com/t/font_1467357626_5109937.svg#iconfont") format("svg");
}

.comment-box {
  font-size: 0.187rem;
  position: relative;
  border-bottom: 0.027rem solid #e6e6e6;
}

.comment-box div.comment-info {
  width: 100%;
  height: .92593rem;
  line-height: .92593rem;
  font-size: 0.4rem;
}

.comment-box div.comment-info img {
  float: left;
  width: .92593rem;
  margin: .27778rem 0 0 .27778rem;
  border-radius: 50%;
}

.comment-box div.comment-info div {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding: .09259rem .18519rem;
}

.comment-box div.comment-info div span {
  display: inline-block;
}

.comment-box div.comment-info div span:first-child {
  font-weight: bold;
}

.comment-box div.comment-info div span:last-child {
  padding-right: .27778rem;
  color: #b4b4b4;
}

.comment-box div.comment-info div span:last-child i {
  font-family: "iconfont";
  font-style: normal;
}

.comment-box div.comment-body {
  margin: 0 .92593rem 0 1.38889rem;
}

.comment-box div.comment-body .reply {
  font-weight: bold;
}

.comment-box div.comment-body .reply>span {
  font-weight: normal;
  color: #8c8c8c;
}

