@import url(../assets/css/swiper-3.3.1.min.css);

@font-face {
  font-family: 'iconfont';
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot");
  src: url("//at.alicdn.com/t/font_1467357626_5109937.eot?#iefix") format("embedded-opentype"),url("//at.alicdn.com/t/font_1467357626_5109937.woff") format("woff"),url("//at.alicdn.com/t/font_1467357626_5109937.ttf") format("truetype"),url("//at.alicdn.com/t/font_1467357626_5109937.svg#iconfont") format("svg");
}

.swiper-container {
  position: relative;
}

.swiper-wrapper {
  height: 6.25rem;
}

.swiper-slide {
  background-repeat: no-repeat;
  -webkit-background-size: 100% 100%;
  background-size: 100%;
  background-position: center;
  -webkit-box-shadow: 0 0 5.333rem #323232 inset;
  box-shadow: 0 0 5.333rem #323232 inset;
}

.swiper-slide h1 {
  position: absolute;
  bottom: .64815rem;
  width: 100%;
  font-size: 0.533rem;
  color: #fff;
  padding-left: .46296rem;
}

.swiper-pagination {
  z-index: 3;
}

.swiper-pagination-bullet {
  width: .16667rem;
  height: .16667rem;
  display: inline-block;
  border-radius: 50%;
  background: #5a5a5a;
  opacity: .8;
}

.swiper-pagination-bullet-active {
  opacity: 1;
  background: #f4f4f4;
}

.swiper-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
  opacity: .1;
  -webkit-filter: blur(0.133rem);
  filter: blur(0.133rem);
}


